@font-face {
    font-family: "Roman";
    src: url(./../../public/Fonts/roman_sd/Roman.ttf);
}

@font-face {
    font-family: "Cardinal";
    src: url(./../../public/Fonts/cardinal/Cardinal.ttf);
}

@font-face {
    font-family: "Tradition";
    src: url(./../../public/Fonts/traditian/Traditian.ttf);
}

:root {
    --couleur1: rgb(254, 214, 119);
    --couleur2: rgb(197, 146, 44);
}

* {
    font-family: Roman;
    padding: 0%;
    border: 0px;
    margin: 0%;
    text-decoration: none;
    color: var(--couleur2);
    line-height: 25px;
}

/* HEADER */

.header {
    margin: 0;
    padding: 0;
}

#titre {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    color: var(--couleur1);
    margin: 20px 20px;
}

#navigation {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    padding-bottom: 15px;
    padding-top: 0px;
    border-bottom: 2px solid var(--couleur2);
    flex-wrap: wrap;
}

.navLien {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    padding: 20px;
    color: var(--couleur2);
    border: 2px solid var(--couleur1);
    margin: 0px 10px;
    transition: 0.5s;
    /* border-radius: 50px; */
    text-transform: uppercase;
    margin: 10px;
}

.navLien:hover {
    border: 2px solid var(--couleur2);
}

/* ACCUEIL */

#main {
    /* min-height: 350px; */
    background-image: url(./../../public/Images/papier\ peint3.jpg);
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
}

.presentation {
    background-color: white;
    width: 80%;
    margin-top: 40px;
}

.leConcept {
    text-align: center;
    padding-top: 40px;
    padding-bottom: 40px;

}

.paragraphe {
    text-align: center;
    padding: 40px 100px;
    padding-top: 0px;

}

.specialites {
    background-color: white;
    margin: 40px;
    text-align: center;
    width: 80%;
    text-align: center;
}

.lesSpecialites {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
}

.specialite {
    width: 300px;
    padding-bottom: 50px;
    padding: 50px;
}

.recette {
    word-wrap: break-word;
    padding-bottom: 20px;
}

.description {
    padding-bottom: 30px;
    word-wrap: break-word;
    height: 150px;
}

.imageRecette {
    width: 300px;
    height: 300px;
    /* background-image: url(./../../public/Images/macarons.jpg); */
    background-position: center;
    background-size: cover;
}

/* PRODUITS */

.lePreambule {
    background-color: white;
    width: 80%;
    margin-top: 40px;
    margin-bottom: 40px;
}

.titrePreambule {
    text-align: center;
    padding-top: 40px;
    padding-bottom: 40px;
}

.preambule {
    text-align: center;
    padding: 40px 100px;
    padding-top: 0px;
}

.lesProduits {
    width: 80%;
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-bottom: 40px;
}

.cardProduit {
    margin: 40px;
    width: 350px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border: 2px solid var(--couleur2);
    background-color: white;
}

.titreProduit {
    text-align: center;
    margin: auto;
    padding: 20px;
}

.imageProduit {
    width: 260px;
    height: 260px;
    border: 2px solid var(--couleur1);
    margin: 0px 20px;
    background-position: center;
    background-size: cover;
}

.prixProduit {
    padding: 20px;
}

/* ABOUT */

